<template>
  <div class="card" :class="stateClass" @click="$emit('taskStateChanged', task)">
    <span class="close" @click.stop="$emit('taskDeleted', task)">x</span>
    <p>{{ task.name }}</p>
  </div>
</template>

<script>
export default {
  props: {
    task: {
      type: Object,
      required: true,
    },
  },
  computed: {
    stateClass() {
      return {
        pending: this.task.pending,
        done: !this.task.pending,
      };
    }
  }
};
</script>

<style>
.card {
  font-size: 2rem;
  font-weight: 300;
  position: relative;
  box-sizing: border-box;
  cursor: pointer;
  user-select: none;
  display: flex;
  flex-direction: row;
  width: 350px;
  height: 150px;
  padding: 10px;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
}
.pending {
  border-left: 12px solid #b73229;
  background-color: #f44336;
}
.done {
  border-left: 12px solid #0a8f08;
  background-color: #4caf50;
  text-decoration: line-through;
  color: #ddd;
}
.close {
  position: absolute;
  right: 10px;
  top: 10px;
  font-size: 0.9rem;
  font-weight: 600;
  height: 20px;
  width: 20px;
  border-radius: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}
.pending .close {
  background-color: #b73229;
}
.done .close {
  background-color: #0a8f08;
}
</style>



